import React from "react";
import { motion } from "framer-motion";
import imgereact from "../../img/10329719.png";
import imgecss from "../../img/troubleshoot-icon-15.jpg";
import imgjs from "../../img/7246044.png";
import imgmui from "../../img/abap-2.png";
import imgeredx from "../../img/cc.png";
import imgehtml from "../../img/file+sql+icon-1320183612970878250.png";
import "./Background.css";

const Backgrond = () => {
  return (
    <div className="background">
      <motion.img
        src={imgereact}
        alt=""
        srcset=""
        whileTap={{ scale: 0.8 }}
        drag={true}
        dragConstraints={{ left: 0, right: 250, top: 0, bottom: 50 }}
        initial={{ opacity: 0, y: 100 }}
        animate={{ opacity: 1, y: -100 }}
      />
      <motion.img
        src={imgecss}
        alt=""
        srcset=""
        whileTap={{ scale: 0.8 }}
        drag={true}
        dragConstraints={{ left: 0, right: 0, top: 0, bottom: 50 }}
        initial={{ opacity: 0, y: -100 }}
        animate={{ opacity: 1, y: 100 }}
      />
      <motion.img
        src={imgjs}
        alt=""
        srcset=""
        whileTap={{ scale: 0.8 }}
        drag={true}
        dragConstraints={{ left: 0, right: 100, top: 0, bottom: 50 }}
        initial={{ opacity: 0, y: -100 }}
        animate={{ opacity: 1, y: 100 }}
      />
      <motion.img
        src={imgehtml}
        alt=""
        srcset=""
        whileTap={{ scale: 0.8 }}
        drag={true}
        dragConstraints={{ left: 50, right: 0, top: 50, bottom: 50 }}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
      />
      <motion.img
        src={imgeredx}
        alt=""
        srcset=""
        whileTap={{ scale: 0.8 }}
        drag={true}
        dragConstraints={{ left: 50, right: 0, top: 50, bottom: 50 }}
        initial={{ opacity: 0, y: -100 }}
        animate={{ opacity: 1, y: 100 }}
      />
      <motion.img
        src={imgmui}
        alt=""
        srcset=""
        whileTap={{ scale: 0.8 }}
        drag={true}
        dragConstraints={{ left: 50, right: 0, top: 50, bottom: 50 }}
        initial={{ opacity: 0, y: -100 }}
        animate={{ opacity: 1, y: 100 }}
      />
    </div>
  );
};

export default Backgrond;
