import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Nav.css";
const Nav = () => {
  const [Toggle, setToggle] = useState("fas fa-bars");
  const [Nave, setNave] = useState("");

  const handeltoggle = () => {
    if (Toggle == "fas fa-bars" && Nave == "") {
      setToggle("fas fa-times");
      setNave("active");
    } else {
      setToggle("fas fa-bars");
      setNave("");
    }
  };
  return (
    <div>
      <section className="nav-bar">
        <div className="uper-nav">
          <div className="logo">
            <Link
              onClick={() => {
                setToggle("fas fa-bars");
                setNave("");
              }}
              to="/"
            >
              Remon Ghaly
            </Link>
          </div>
          <div className="info">
            <ul>
              <li></li>
              <li>
                <a
                  style={{
                    fontWeight: "600",
                    fontSize: "14px",
                  }}
                  href="https://drive.google.com/file/d/1nW21aIKp880INn_fphYUm2fkG_T2d1Pp/view"
                  target="_blank"
                >
                  CV
                </a>
              </li>

              {/* <li>
                <a href="https://github.com/GergesGhaly" target="_blank">
                  <i className="fab fa-github"></i>
                </a>
              </li>
 */}
              <li>
                <a
                  href="https://www.linkedin.com/in/remon-ghaly-08a0b114a/"
                  target="_blank"
                >
                  <i className="fab fa-linkedin"></i>
                </a>
              </li>
              <li onClick={handeltoggle} className="toggle">
                <i class={Toggle}></i>
              </li>
            </ul>
          </div>
        </div>
      </section>
      <div className={`ul-nav ${Nave}`}>
        <ul>
          <li>
            <Link onClick={handeltoggle} to="/">
              Home
            </Link>
          </li>
          <li>
            <Link onClick={handeltoggle} to="productlist">
              Experiences
            </Link>
          </li>
          <li>
            <Link onClick={handeltoggle} to="about">
              About Me
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Nav;
