import React, { useEffect } from "react";
import "./about.css";
import img2 from "../../img/personal.jpg";
import AOS from "aos";
import "aos/dist/aos.css";

const About = () => {
  useEffect(() => {
    AOS.init();
  }, []);

  return (
    <div className="about">
      <div className="about-left" data-aos="fade-right">
        {/* <div className="card bg"></div> */}
        <div className="card">
          <img src={img2} alt="" />
        </div>
      </div>
      <div className="about-right" data-aos="fade-left">
        <h1 className="about-title">About Me</h1>
        <p className="about-desc">
          <span>1 - WHO I AM</span>
          <br /> My name is Remon Ghaly Bebawi. I was born in 1994 .<br /> I
          graduated from the Abbasiya Institute for Systems and Information
          <br /> with a grade of very good
          <br />
          <br />
          <span>2 - SKILLS</span>
          <ul>
            <li> SAP </li>
            <li>SAP APAB </li>
          </ul>
          <br />
          <span>3 - PASSION</span>
          <br /> coding , Design , reading
        </p>
      </div>
    </div>
  );
};

export default About;
