import React, { useEffect, useState } from "react";
import Product from "../product/Product";
import "./productlist.css";
import product from "../../Data";
import AOS from "aos";
import "aos/dist/aos.css";

const Productlist = () => {
  const [filter, setfilter] = useState("");
  const [products, setproduct] = useState([]);

  // const handelfilterAll=()=>{
  //   // const All=products.filter(item=>item.type === '')
  //   setproduct(product)
  //   }

  useEffect(() => {
    AOS.init();
    setproduct(product);
  }, []);
  const handelfilter = (e) => {
    // const All=products.filter(item=>item.type === '')
    setfilter(e);
  };

  return (
    <div className="Productlist">
      <div className="Productlist-text" data-aos="fade-left">
        <h1 className="Productlist-header">My Experiences</h1>
        <p className="Productlist-desc">
          Some work experience and certificates
        </p>
        <div data-aos="fade-down">
          <button onClick={() => handelfilter("")}>All</button>
          <button onClick={() => handelfilter("Work")}>Work experiences</button>
          <button onClick={() => handelfilter("Certificates")}>
            Certificates
          </button>
        </div>
      </div>
      <div className="Productlist-list">
        {products
          .filter((item) => item.type?.includes(filter))
          .map((pro, num) => (
            <Product
              num={num}
              key={pro.id}
              id={pro.id}
              git={pro?.githup}
              tutLink={pro?.tutLink}
              img={pro.img}
              link={pro.href}
              desc={pro.desc}
              date={pro.date}
              pro={pro}
            />
          ))}
      </div>
    </div>
  );
};

export default Productlist;
